import Layout from '../components/Layout.js'
import withData from '../components/Apollo'
import { graphql } from 'react-apollo'
import gql from 'graphql-tag'

function TeamList({
    data: { error, allTeams }
  }) {
    if (error) {
        return (
            <div>
                <h2>Error</h2>
                <p>{error.message}</p>
            </div>
        )
    }        
    
    if (allTeams && allTeams.length) {
      return (
        <Container>
          {allTeams.map((team) => (<pre>{team}</pre>))}
        </Container>
      )
    }

    return <div>Loading...</div>
  }

const allTeams = gql`{
    movies(query: "Star Wars") {
        id
        title
        overview
    }
}
`

const Teams = graphql(allTeams, {
    props: ({ data }) => ({
      data,
    })
  })(TeamList)
  
export default withData(() => (
    <Layout>
       <Teams></Teams>
    </Layout>
))